import React from 'react';
import { Button, Grid, Header, Segment, Sidebar } from 'semantic-ui-react';
import 'semantic-ui-css/semantic.min.css';

import SvgMap from './SvgMap';

import './App.css';

export const sideBarContext = React.createContext();

function SidebarProvider(props) {
  const [visible, setVisible] = React.useState(false);
  const [title, setTitle] = React.useState('Pixels for sale');
  const [description, setDescription] = React.useState('Pixels for sale');
  const [url, setUrl] = React.useState('');

  const SideBarContext = sideBarContext;

  return (
    <SideBarContext.Provider
      value={{
        visible,
        setVisible,
        title,
        setTitle,
        description,
        setDescription,
        url,
        setUrl
      }}
    >
      {props.children}
    </SideBarContext.Provider>
  );
}

const HorizontalSidebar = ({ animation, direction, visible }) => {
  let {
    description,
    setDescription,
    setTitle,
    setVisible,
    title,
    url
  } = React.useContext(sideBarContext);

  return (
    <Sidebar
      as={Segment}
      animation={animation}
      direction={direction}
      visible={visible}
      onHide={() => {
        setDescription('Pixels for sale');
        setTitle('Pixels for sale');
        setVisible(false);
      }}
    >
      <Grid textAlign="center">
        <Grid.Row columns={1}>
          <Grid.Column>
            <Header as="h1">{`${title} ❤️ Breasts`}</Header>
          </Grid.Column>
        </Grid.Row>
        <Grid.Row columns={2} divided>
          <Grid.Column width={10}>
            <Header as="h3">Description</Header>
            <div style={{ textAlign: 'left' }}>{description}</div>
          </Grid.Column>
          <Grid.Column width={2}>
            <Button
              primary
              onClick={() => {
                window.open(url);
              }}
            >
              Visit
            </Button>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </Sidebar>
  );
};
function Content() {
  let { visible } = React.useContext(sideBarContext);
  return (
    <Sidebar.Pushable as={Segment} style={{ overflowY: 'hidden' }}>
      <HorizontalSidebar
        animation={'overlay'}
        direction={'bottom'}
        visible={visible}
      ></HorizontalSidebar>

      <Sidebar.Pusher dimmed={visible}>
        <Segment
          style={{
            display: 'flex',
            justifyContent: 'center'
          }}
        >
          <SvgMap />
        </Segment>
      </Sidebar.Pusher>
    </Sidebar.Pushable>
  );
}

function App() {
  return (
    <SidebarProvider>
      <Content />
    </SidebarProvider>
  );
}

export default App;
